
#table {
  border: 1px solid rgba(0, 40, 100, 0.12);
  margin: auto;
  background-color: white;
  display: table;
  width: 100%
}
.table {
  width: 80%;
  margin: auto;
}
.t-row {
  text-align: -webkit-match-parent;
}
.leftbar {
  left: 0;
  margin: 1%;
  position: fixed;
  z-index: 999;
}
.clear {
  position: absolute;
  right: 0.9em;
  margin-top: 0.5em;
}
.inputs {
  padding: 3%;
  margin: auto;
  margin-bottom: 4%;
  width: 79%;
}
thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  cursor: pointer;
}
.sorting:after { 
  bottom: 5px;
  content: "\e92d";
  font-family: 'feather' !important;
}
.sorting:before {
  top: 5px;
  content: "\e930";
  font-family: 'feather' !important;
}
.thead-light {
  color: #495057;
  background-color: #e9ecef;
  border-color: rgba(0, 40, 100, 0.12);
}
.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.title {
  font-family:monospace;
  font-style: oblique;
  font-stretch:extra-expanded;
  font-size: 4em;
  text-align: center;
  color:  cornflowerblue;
  padding-top: 1em;
}





